import React,{useState} from 'react'
import './TweetBox.css'
import {Avatar, Button} from "@material-ui/core"
import db from './firebase'

function TweetBox() {
    const [tweetMessage, setTweetMessage] = useState('');
    const [tweetImage, setTweetImage] = useState('');
    const sendTweet = e => {
    e.preventDefault();
    db.collection('posts').add({
    displayname : 'Sudalai',
    username : 'dreamworld-tamil',
    verified : true,
    text : tweetMessage,
    image : tweetImage,
    avatar : '',
    timestamp : new Date()
    });

    setTweetImage("");
    setTweetMessage("");
    }
    return (
        <div className="tweetBox">
        <form>
        <div className="tweetBox__input">
        <Avatar src="" />
        <input placeholder="what's happening?" value={tweetMessage} type="text" 
        onChange={ e => setTweetMessage(e.target.value) } />
        </div>
        <input value={tweetImage} onChange={ e => setTweetImage(e.target.value) } 
        className="tweetBox__imageInput" placeholder="Optional: Enter Image Url" type="text" /> 
        <Button onClick={sendTweet} className="tweetBox__tweetButton">Tweet</Button>
        </form>
        </div>
    )
}

export default TweetBox
